<template>
    <div class="message">
        <div v-if="$route.name === '回复消息'">
            <Button class="create" type="primary" to="/manage_test/gonghao/message/create">添加新消息</Button>
            <Table class="message-table" stripe :columns="msgHeader" :data="msgData"></Table>
        </div>
        <router-view />
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msgData: [],
                msgHeader: [
                    {
                        key: 'msgId',
                        title: 'msgId',
                        align: 'center',
                    }, {
                        key: 'type',
                        title: '类型',
                        align: 'center',
                        render: (h, params) => {
                            let type = (params.row.type == 0 ? '文本消息' : '图文消息');
                            return h('span', {
                                props: {}
                            }, type)
                        }
                    }, {
                        title: '标题',
                        align: 'center',
                        render: (h, params) => {
                            let title = params.row.contents[0].title;
                            return h('span', {
                                props: {}
                            }, title)
                        }
                    }, {
                        title: '详细信息',
                        align: 'center',
                        render: (h, params) => {
                            let description = params.row.contents[0].description;
                            return h('span', {
                                props: {}
                            }, description)
                        }
                    }, {
                        title: '链接',
                        align: 'center',
                        render: (h, params) => {
                            let url = params.row.contents[0].url;
                            return h('span', {
                                props: {}
                            }, url)
                        }
                    }, {
                        title: '图片',
                        align: 'center',
                        render: (h, params) => {
                            return h('img', {
                                attrs: {
                                    src: params.row.contents[0].picurl,
                                    style: 'width:100%;padding:10px;'
                                }
                            })
                        }
                    }, {
                        title: 'Action',
                        align: 'center',
                        width: 200,
                        render: (h, params) => {
                            let that = this;
                            return h('div', [
                                h('Button', {
                                    style: {
                                        marginRight: '10px'
                                    },
                                    props: {
                                        type: 'warning',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.$axios.post('/msg/create', {
                                                contents: params.row.contents,
                                                type: params.row.type,
                                                description: params.row.description
                                            }).then(res => {
                                                that.showMessage();
                                            });
                                        }
                                    }
                                }, '复制'),
                                h('Button', {
                                    style: {
                                        marginRight: '10px'
                                    },
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.$router.push({
                                                name: '编辑回复消息',
                                                params: params.row
                                            })
                                        }
                                    }
                                }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click() {
                                            that.$axios.get('/msg/del', {params: {id: row._id}})
                                            .then(res => {
                                                that.$Message.info(res.data.success);
                                                that.showMessage();
                                                that.msgData.splice(params.index, 1);
                                            });
                                        }
                                    }
                                }, '删除'),
                            ])
                        }
                    }
                ]
            };
        },
        mounted() {
            this.showMessage();
            // sessionStorage.setItem("secName", this.$route.name);
        },
        methods: {
            showMessage() {
                this.$axios.get('/msg')
                .then(res => {
                    if (res.data.data.length != 0) {
                        this.msgData = res.data.data;
                    }
                });
            }
        }
    };
</script>

<style scoped>
    .create {
        margin-bottom: 30px;
        float: left;
    }
    .message-table {
        clear: both;
    }
</style>

